<template>
  <div class="container">
    <form class="loginForm">
      <label for="account">ËØ∑ËæìÂÖ•Ë¥¶Âè∑</label>
      <el-input
        id="account"
        class="input"
        tabindex="1"
        v-model.lazy="userAccount"
        prefix-icon="el-icon-user"
        placeholder="Ë¥¶Âè∑"
        clearable
        autofocus
      ></el-input>
      <label for="password">ËØ∑ËæìÂÖ•ÂØÜÁ†Å</label>
      <router-link to="/forget" class="forget">ÂøòËÆ∞ÂØÜÁ†ÅÔºü</router-link>
      <el-input
        id="password"
        class="input"
        tabindex="2"
        v-model="passWord"
        prefix-icon="el-icon-lock"
        placeholder="ÂØÜÁ†Å"
        show-password
      ></el-input>
      <div class="captchaCode">
        <el-input v-model="codeNumber" class="code_input" tabindex="3" placeholder="È™åËØÅÁ†Å"></el-input>
        <img v-show="captchaCodeImg" :src="captchaCodeImg" @click="getCaptchaCode" />
        <p @click="getCaptchaCode">Êç¢‰∏ÄÂº†</p>
      </div>
      <el-button ba type="primary" id="login" tabindex="4" round @click="Login">ÁôªÂΩï</el-button>
    </form>
    <p class="login_tips">TipÔºöÊú™Ê≥®ÂÜåËøáÁöÑË¥¶Âè∑ÔºåÁôªÂΩïÊó∂Â∞ÜËá™Âä®Ê≥®ÂÜåÔºåÊ≥®ÂÜåËøáÁöÑÁî®Êà∑ÂèØÂá≠Ë¥¶Âè∑ÂØÜÁ†ÅÁôªÂΩï</p>
  </div>
</template>

<script>
    import { getStore } from '../../config/mUtils'
    import { mapState,mapMutations } from 'vuex'
    import { getcaptchas, accountLogin } from '../../api/getData'

    export default {
        data(){
            return {
                userInfoList: null, //Ëé∑ÂèñÂà∞ÁöÑÁî®Êà∑‰ø°ÊÅØ
                userAccount: null, //Áî®Êà∑Âêç
                passWord: null, //ÂØÜÁ†Å
                captchaCodeImg: null, //È™åËØÅÁ†ÅÂú∞ÂùÄ
                codeNumber: null, //È™åËØÅÁ†Å
            }
        },
        computed:{
            ...mapState([
                'userInfo'
            ])
        },
        created(){
            this.getCaptchaCode();
        },
        beforeMount(){
            // if(getStore('user_id')){
            //     this.$router.push('/profile')   //ÂΩìÊúâÁî®Êà∑ÁôªÂΩïÊó∂Ë∑≥ËΩ¨Âà∞profileÈ°µÈù¢
            // }
        },
        methods: {
            ...mapMutations([
                'RECORD_USERINFO',
            ]),
            //Ëé∑ÂèñÈ™åËØÅÂêóÔºåÁ∫ø‰∏äÁéØÂ¢É‰ΩøÁî®Âõ∫ÂÆöÁöÑÂõæÁâáÔºåÁîü‰∫ßÁéØÂ¢É‰ΩøÁî®ÁúüÂÆûÁöÑÈ™åËØÅÁ†Å
            async getCaptchaCode(){
                let res = await getcaptchas();
                this.captchaCodeImg = res.data.code;
            },
            //ÂèëÈÄÅÁôªÂΩï‰ø°ÊÅØ
            async Login() {
                if (!this.userAccount) {
                    //element Ê∂àÊÅØÊèêÁ§∫
                    this.$message({
                        type: 'warning',
                        showClose: true,
                        message: 'ËØ∑ËæìÂÖ•ÔºöÁî®Êà∑Âêç üëáüëáüëá'
                    });
                    return  //ÈáçÊñ∞ÂºÄÂßã
                }else if(!this.passWord){
                    this.$message({
                        type: 'warning',
                        showClose: true,
                        message: 'ËØ∑ËæìÂÖ•ÔºöÂØÜÁ†Å üëáüëáüëá'
                    });
                    return
                }else if(!this.codeNumber){
                    this.$message({
                        type: 'warning',
                        showClose: true,
                        message: 'ËØ∑ËæìÂÖ•ÔºöÈ™åËØÅÁ†Å üëáüëáüëá'
                    });
                    return
                }
                //Áî®Êà∑ÂêçÁôªÂΩï
                accountLogin(this.userAccount, this.passWord, this.codeNumber).then((res) => {
                    this.userInfoList = res.data
                    console.log(res.data)
                    //Â¶ÇÊûúËøîÂõûÁöÑÂÄº‰∏çÊ≠£Á°ÆÔºåÂàôÂºπÂá∫ÊèêÁ§∫Ê°ÜÔºåËøîÂõûÁöÑÂÄºÊ≠£Á°ÆÂàôËøîÂõû‰∏ä‰∏ÄÈ°µ
                    if (!this.userInfoList.user_id) {
                        this.$message({
                            type: 'error',
                            showClose: true,
                            message: this.userInfoList.message
                        });
                        this.getCaptchaCode();
                    }else{
                        this.$message({
                            type: 'success',
                            showClose: true,
                            message: 'ÂØÜÁ†ÅÊ≠£Á°ÆÔºåÁôªÂΩïÊàêÂäüÔºÅ',
                        });
                        this.RECORD_USERINFO(this.userInfoList);
                        this.$router.go(-1);
                    }
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
@import "../../style/mixin";
